<template>
  <Navbar/>
  <section class="gallery">
    <h1>Gallery</h1>
    <div class="gallery-container">
      <!-- Loop through the images using v-for -->
      <div class="gallery-item" v-for="index in 42" :key="index">
        <img
          :src="require(`@/assets/images/gallery/gallery-image (${index}).jpg`)"
          :alt="'Gallery Image ' + index"
          @click="openPreview(index)"  
        />
      </div>
    </div>

    <!-- Modal for image preview -->
    <div v-if="isModalOpen" class="modal" @click.self="closePreview">
      <div class="modal-content">
        <img :src="previewImage" alt="Preview" />
        <button class="close-btn" @click="closePreview">x</button>
      </div>
    </div>
  </section>
</template>

<script>
import Navbar from '@/components/Navbar.vue';
import Footer from '@/components/Footer.vue';

export default {
  name: "GalleryView",
  components: { Navbar, Footer },
  data() {
    return {
      isModalOpen: false, // Controls modal visibility
      previewImage: '' // Holds the image source for the modal
    };
  },
  methods: {
    // Open the modal and set the image for preview
    openPreview(index) {
      this.previewImage = require(`@/assets/images/gallery/gallery-image (${index}).jpg`);
      this.isModalOpen = true;
    },
    // Close the modal
    closePreview() {
      this.isModalOpen = false;
      this.previewImage = ''; // Reset the preview image
    }
  }
};
</script>

<style scoped lang="scss">
.gallery {
  padding: $vertical-padding $horizontal-padding;
  h1 {
    width: 100%;
    text-align: center;
    padding: 64px 0;
  }
}

.gallery-container {
  display: grid;
  grid-template-columns: repeat(3, 1fr); /* 3 columns */
  gap: 20px; /* Space between the images */
}

.gallery-item img {
  width: 100%;
  height: auto;
  border-radius: 8px; /* Optional, adds rounded corners */
  cursor: pointer; /* Indicate that the image is clickable */
}

/* Modal Styles */
.modal {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.7); /* Dark background */
  display: flex;
  justify-content: center;
  align-items: start;
  z-index: 1000;
  
}

.modal-content {
  position: relative;
//   background: white;
  padding: 20px;
  max-width: 37%;
  max-height: 37%;
}

.modal-content img {
  width: 100%;
  height: auto;
  border-radius: 8px;
}

.close-btn {
  position: absolute;
  top: 10px;
  right: 10px;
  background: rgb(109, 33, 33);
  color: white;
  border: none;
  padding: 10px;
  cursor: pointer;
  border-radius: 100%;
  padding:12px 16px;
}
</style>
